* {
  box-sizing: border-box;
}

body, header, aside, main, section, article, form, label, input, textarea, span, div, nav, h1, h2, h3, p, a, li, ul, picture, img, source, button {
  vertical-align: baseline;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

:after, :before {
  box-sizing: border-box;
  display: block;
}

@keyframes content-tabs {
  from {
    opacity: 0;
    transform: translateX(-5%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes top-bar {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes left-to-right {
  from {
    opacity: 0;
    transform: translateX(-150%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes right-to-left {
  from {
    opacity: 0;
    transform: translateX(150%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes img {
  from {
    opacity: 0;
    height: 0;
  }

  to {
    opacity: 1;
    height: 100%;
  }
}

.page {
